<template>
  <div>
    <legend>Text QR Code</legend>
    <div class="form-group">
      <label for="comment">Text:</label>
      <textarea class="form-control" rows="5" id="comment" v-model="qrcode.text.text"></textarea>
    </div>
  </div>
</template>
<script>
  export default {
  props: ['qrcode']
  }
</script>



