<template>
  <div>
    <div class="row">
      <div class="col-md-10 col-sm-offset-1">
        <form class="form-horizontal" role="form">
          <fieldset>

            <!-- Form Name -->
            <legend>Email QR Code</legend>

            <!-- Text input-->
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Email</label>
              <div class="col-md-10">
                <input v-model="qrcode.email.email" type="text" placeholder="email" class="form-control">
              </div>
            </div>

            <!-- Text input-->
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Subject</label>
              <div class="col-md-10">
                <input v-model="qrcode.email.subject" type="text" placeholder="subject" class="form-control">
              </div>
            </div>

            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Message</label>
              <div class="col-md-10">
                <textarea v-model="qrcode.email.text" class="form-control" rows="5" id="comment"></textarea>
              </div>
            </div>

          </fieldset>
        </form>
      </div>
  </div>
  </div>
</template>
<script>
  import mix from '../mixins'
  export default {
    props: ['qrcode']
  }
</script>



