<template>
  <div>
    <div class="row">
      <div class="col-md-10 col-sm-offset-1">
        <form class="form-horizontal" role="form">
          <fieldset>

            <!-- Form Name -->
            <legend>SMS QR Code</legend>

            <!-- Text input-->
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Number</label>
              <div class="col-md-10">
                <input  v-model="qrcode.sms.phone" type="text" placeholder="Your number" class="form-control">
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Message</label>
              <div class="col-md-10">
                <textarea v-model="qrcode.sms.text" class="form-control" rows="5" id="comment"></textarea>
              </div>
            </div>

          </fieldset>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: ['qrcode']
  }
</script>



