export const mix = {
  watch: {
    qrcode: {
      handler(newValue, oldValue) {
        if (this.tab === 'url') {
          this.qrcode.url = `https://chart.googleapis.com/chart?cht=qr&chl=${newValue.link.url}&chs=230x230&choe=UTF-8&chld=L|2`
        }

        else if (this.tab === 'email') {

          this.qrcode.url = `https://chart.googleapis.com/chart?cht=qr&chl=MATMSG%3ATO%3A${newValue.email.email}%3BSUB%3A${newValue.email.subject}%3BBODY%3A${newValue.email.text}%3B%3B&chs=230x230&choe=UTF-8&chld=L|2`
        }

        else if (this.tab === 'sms') {

          this.qrcode.url = `https://chart.googleapis.com/chart?cht=qr&chl=SMSTO%3A${newValue.sms.phone}%3A${newValue.sms.text}&chs=230x230&choe=UTF-8&chld=L|2`
        }

        else if (this.tab === 'message') {
          this.qrcode.url = `https://chart.googleapis.com/chart?cht=qr&chl=${newValue.text.text}&chs=230x230&choe=UTF-8&chld=L|2`
        }

        else if (this.tab === 'vcard') {

          this.qrcode.url = `https://chart.googleapis.com/chart?cht=qr&chl=BEGIN%3AVCARD%0AVERSION%3A3.0%0AN%3A${newValue.vcard.lastname}%3B${newValue.vcard.firstname}%0AFN%3A${newValue.vcard.firstname}%20${newValue.vcard.lastname}%0AORG%3A${newValue.vcard.company}%0ATITLE%3A${newValue.vcard.job}%0AADR%3A%3B%3B${newValue.vcard.street}%3B${newValue.vcard.city}%3B${newValue.vcard.state}%3B%3B${newValue.vcard.country}%0ATEL%3BWORK%3BVOICE%3A${newValue.vcard.phone}%0ATEL%3BFAX%3A${newValue.vcard.fax}%0AEMAIL%3BWORK%3BINTERNET%3A${newValue.vcard.email}%0AEND%3AVCARD%0A&chs=230x230&choe=UTF-8&chld=L|2`
        }
        localStorage.setItem('qr-barcode-generator', JSON.stringify(newValue));
        localStorage.setItem('qr-barcode-generator-tab', this.tab);

      },
      deep: true
    }
  }
}



