<template>
  <div id="app">
    <div id="throbber"></div>
    <div id="noty-holder"></div>
    <div id="wrapper">
      <!-- Navigation -->
      <nav class="navbar navbar-inverse navbar-fixed-top" role="navigation">
      </nav>


      <div id="page-wrapper">
        <div class="container-fluid">

          <!-- Page Heading -->
          <!-- /.row -->
          <div class="row center-block">
            <div class="col-md-8">
              <div class="panel with-nav-tabs panel-primaryes" style="background-color: white">
                <div class="panel-heading">
                  <ul class="nav nav-tabs" style="background-color: #c0c4c7">
                    <li v-for="i in tabs.name.length" :class="[tab == tabs.name[i-1].toLowerCase()? 'active' : 'dropdown']" @click="tab=tabs.name[i-1].toLowerCase()" style="font-size: 15px"><a data-toggle="tab"><span :class="tabs.fa[i-1]"></span> {{tabs.name[i-1]}}</a></li>
                  </ul>
                </div>

                <div class="panel-body">
                  <div class="tab-content">
                    <div class="tab-pane fade in active">
                        <component :is="tab" :qrcode="qrcode"></component>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4" style="background-color: white; border-radius: 5px">
              <div class="container-fluid">
                <div class="row">
                 <img v-if="qrcode.url.length == 0" src="https://www.qr-code-generator.com/wp-content/themes/qr/images/banners/frames/websiteQRCode_noFrame.png" style="width: 250px; height: 250px; opacity: 0.2"/>
                  <a :href="qrcode.url" target="_blank" download>
                   <img v-if="qrcode.url.length != 0" :src="qrcode.url"/>
                  </a>
                </div>
                <div class="row">
                  <button class="btn btn-info" @click="printImg" style="margin-bottom: 10px">Print</button>
                </div>
                <div class="row">
                  <a class="btn btn-info" :href="qrcode.url" target="_blank" style="margin-bottom: 10px" download>Download</a>
                </div>
                <div class="row">
                  <a data-toggle="modal" data-target="#exampleModal" role="button">Embeded qr code</a>
                </div>
            </div>
          </div>
          </div>

        </div>
      </div>
      <!-- /#page-wrapper -->
    </div><!-- /#wrapper -->
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Embeded</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <textarea class="form-control" rows="5" id="comment" v-model="qrcode.url.replace(/ /g, '%20')"></textarea>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import {mix} from './mixins'
import Sms from './components/Sms.vue'
import Email from './components/Email.vue'
import Text from './components/Text.vue'
import Url from './components/Url.vue'
import Vcard from './components/Vcard.vue'
export default{
  mixins: [mix],
  data(){
    return{
      qrcode: {
        sms:{
          phone: '',
          text: ''
        },
        email: {
          text: '',
          email: '',
          subject: ''
        },
        text:{
          test: ''
        },
        link:{
          url: ''
        },
        vcard:{
          firstname: '',
          lastname: '',
          phone: '',
          fax: '',
          email: '',
          company: '',
          job: '',
          city: '',
          street: '',
          state: '',
          country: ''
        },
        url: ''
      },
      tabs:{
        name:['Url','VCard', 'Message', 'Email', 'SMS'],
        fa: ['fas fa-external-link-alt', 'fas fa-address-card', 'fa fa-align-left', 'fas fa-at', 'fas fa-envelope']
      },
      tab: 'url',
      code: null
    }
  },
  components: {
    url: Url,
    vcard: Vcard,
    message: Text,
    email: Email,
    sms: Sms
  },
  mounted(){
    let code = JSON.parse(localStorage.getItem('qr-barcode-generator'));
    let tab = localStorage.getItem('qr-barcode-generator-tab')
    if(code && tab) {
      this.qrcode = code
      this.tab = tab
    }
  },
  methods:{
    printImg(){
      var win = window.open('');
      win.document.write('<img src="' + this.qrcode.url + '" onload="window.print();window.close()" />');
      win.focus();
    }
  }
}
function loadWidget(widget){
  widget += '.vue'
  console.log("Loaded " + widget);
  return System.import('./components/' + widget);
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #c0c4c7;
  margin-top: 60px;
}
</style>



