<template>
  <div>
    <div class="row">
      <div class="col-md-10 col-sm-offset-1">
        <form class="form-horizontal" role="form">
          <fieldset>

            <!-- Form Name -->
            <legend>VCard QR Code</legend>

            <!-- Text input-->
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Name</label>
              <div class="col-md-5">
                <input v-model="qrcode.vcard.firstname" type="text" placeholder="Firstname" class="form-control">
              </div>
              <div class="col-md-5">
                <input v-model="qrcode.vcard.lastname" type="text" placeholder="Lastname" class="form-control">
              </div>
            </div>

            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Numbers</label>
              <div class="col-md-5">
                <input v-model="qrcode.vcard.phone" type="text" placeholder="Phone" class="form-control">
              </div>
              <div class="col-md-5">
                <input  v-model="qrcode.vcard.fax" type="text" placeholder="Fax" class="form-control">
              </div>
            </div>
            <!-- Text input-->
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Email</label>
              <div class="col-md-10">
                <input v-model="qrcode.vcard.email" type="text" placeholder="your#email.com" class="form-control">
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Company</label>
              <div class="col-md-5">
                <input v-model="qrcode.vcard.company" type="text" placeholder="Company" class="form-control">
              </div>
              <div class="col-md-5">
                <input v-model="qrcode.vcard.job" type="text" placeholder="Job" class="form-control">
              </div>
            </div>

            <!-- Text input-->
            <div class="form-group">
            <label class="col-md-2 control-label" for="textinput">City</label>
            <div class="col-md-10">
              <input  v-model="qrcode.vcard.city" type="text" placeholder="City" class="form-control">
            </div>
          </div>

            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Street</label>
              <div class="col-md-10">
                <input v-model="qrcode.vcard.street" type="text" placeholder="Street" class="form-control">
              </div>
            </div>

            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">State</label>
              <div class="col-md-10">
                <input v-model="qrcode.vcard.state" type="text" placeholder="State" class="form-control">
              </div>
            </div>
            <!-- Text input-->
            <div class="form-group">
              <label class="col-md-2 control-label" for="textinput">Country</label>
              <div class="col-md-10">
                <input v-model="qrcode.vcard.country" type="text" placeholder="Country" class="form-control">
              </div>
            </div>

          </fieldset>
        </form>
      </div><!-- /.col-lg-12 -->
    </div><!-- /.row -->
  </div>
</template>

<script>
  export default {
    props: ['qrcode']
  }
</script>



